.recipe {
  border-radius: 10px;
  box-shadow: 0px 5px 20px rgb(87, 85, 85);
  margin: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  background: rgb(255, 255, 255);
  align-items: center;
  min-width: 45%;
  max-width: 45%;
  text-align: center;
}
.pic {
  padding: 20px;
  height: 190px;
  width: 190px;
  border-radius: 50%;
}
h1 {
  font-size: 40px;
  text-decoration: underline;
}
h2 {
  font-size: 30px;
}
li {
  font-size: 20px;
  color: rgb(8, 8, 8);
  padding-right: 10px;
}
ul {
  list-style-type: none;
}
p {
  font-size: 20px;
}
