.App {
  min-height: 100vh;
  background-image: linear-gradient(120deg, #d4fc79 0%, #96e6a1 100%);
}

.headingForm {
  text-align: center;
  font-size: 30px;
  color: rgb(0, 0, 0);
  padding: 1px;
}

.searchForm {
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.searchBar {
  width: 30%;
  border: none;
  padding: 10px;
}

.searchButton {
  background: #f5f5f5;
  border: none;
  border-radius: 10px;
  padding: 10px;
  cursor: pointer;
  margin-left: 10px;
}

.searchButton:hover {
  background: #03ff25;
  box-shadow: 0px, 4px, 4px #000000;
  border: none;
  border-radius: 10px;
  padding: 10px;
  cursor: pointer;
}

.contents {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.container {
  display: flex;

  /* If you want to place the icon before the text input */
  flex-direction: row-reverse;

  /* Border */
  border: 1px solid rgba(0, 0, 0, 0.3);
}

.container__input {
  border-color: transparent;
  /* Take available width */
  flex: 1;
}
